/* WeatherDashboard.css */

.weather-dashboard {
  display: flex;
  height: 100vh;
}

.left-panel {
  flex: 1;
  background-color: #f0f0f0;
  padding: 20px;
}

.right-panel {
  flex: 3;
  display: flex;
  flex-direction: column;
}

.top-right {
  flex: 1;
  background-color: #ffffff;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.bottom-right {
  flex: 3;
  background-color: #ffffff;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.weather-info {
  /* Estilos para la información del clima en el panel superior derecho */
}

/* Estilos para dividir el panel superior e inferior del lado derecho */
.right-panel {
  flex: 3;
  display: flex;
  flex-direction: column;
}

.top-right {
  flex: 1;
  background-color: #ffffff;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.temperature-chart {
  text-align: center;
  margin: 20px;
}

.chart {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 400px; /* Ajusta la altura según tus necesidades */
}

.bar {
  width: 20px;
  background-color: #3498db; /* Cambia el color de las barras según tus preferencias */
  text-align: center;
  padding: 5px 0;
  margin-right: 5px;
  font-size: 12px;
  color: white;
  transition: height 0.3s ease; /* Agrega una animación de transición */
}

.temperature-chart-small {
  height: 300px; /* Ajusta la altura a tu preferencia */
}